.TaskTextContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-positive: 2;
      flex-grow: 2;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}

.TaskText {
  font-family: 'Segoe UI', Lucinda Grande, Geneva, Verdana, sans-serif;
  color: #EEE;
  font-Size: 12pt;
  margin-left: 8px;
}

.TaskText[data-ishighpriority*="true"] {
  color: #D11;
  font-weight: 700;
  font-stretch: 1;
}

.Fade {
  opacity: 0.15;
  font-weight: 200;
}

.TaskTextInput {
  width: 300px;
  font-family: 'Segoe UI', Lucinda Grande, Geneva, Verdana, sans-serif;
  color: #FFF;
  background: rgba(100,100,100,255);
  border: 0px solid #FFF;
}


