.SidebarToolbar {
    font-family: 'Segoe UI', Lucinda Grande, Geneva, Verdana, sans-serif;
    font-Size: 16pt;
    color: #FFF;
    cursor: pointer;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
}

.SidebarVerticalFlexContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: start;
        justify-content: flex-start;
    -ms-flex-align: start;
        align-items: flex-start;
}

.ProjectSelectorsFlexItemContainerOpen {
    -ms-flex-positive: 1;
        flex-grow: 1;
}

.ProjectSelectorsFlexItemContainerCollapsed {
    display: none;
    
}

.CollapseButtonFlexItemContainer {
    -ms-flex-positive: 0;
        flex-grow: 0;
    width: 25px;
}

.SidebarOpen {
    z-index: 3;
    position: relative; /* So .SidebarCollapseButtonContainer absolute position Works */
    background: rgba(20,20,20,255);
    width: 100vw;
    height: 100vh;
    float: left;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
}

.SidebarCollapsed {
    width: 25px;
    height: 100vmax;
}

.SidebarCollapseButton {
    background: rgba(50,50,50,255);
    height: 100vh;
}




