/* TODO: Clean up these Selectors, shift the Position and Sizing up to .TaskContainer */
.TaskContainer {
  height: auto;
  overflow: visible;
  min-height: 40px;
  max-height: 120px; /* Same as .TaskClickContainer max-height */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-bottom: 4px;
}

.TaskInactiveStyle {
  background: rgba(0,0,0,0);
  color: #FFF;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
}


.TaskActiveStyle {
  background: rgba(63,63,76,255);
  color: #FFF;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.TaskMovingStyle {
  background: rgba(63,63,180,255);
  color: #FFF;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -webkit-animation: PingMovingTask 0.25s linear 1;
          animation: PingMovingTask 0.25s linear 1;
}

@-webkit-keyframes PingMovingTask {
  100% { background: rgba(63,63,220,255)}
}

@keyframes PingMovingTask {
  100% { background: rgba(63,63,220,255)}
}

.TaskClickContainer {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  min-height: 40px;
  max-height: 120px;  /* Same as .TaskContainer max-height */
  overflow: hidden;
  
}





