.TaskCheckBoxContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-positive: 1;
      flex-grow: 1;
}

.TaskCheckBoxIsNotChecked {
  -ms-flex-negative: 1;
      flex-shrink: 1;
  width: 15px;
  height: 15px;
  border: 1px solid #DDD;
  margin-left: 5px;
  -webkit-animation: isDeChecking 0.25s linear 1;
          animation: isDeChecking 0.25s linear 1;
}

@-webkit-keyframes isDeChecking {
  0% { background: #EEE; }
  100% { background: transparent; }
}

@keyframes isDeChecking {
  0% { background: #EEE; }
  100% { background: transparent; }
}

.TaskCheckBoxIsChecked {
  -ms-flex-negative: 1;
      flex-shrink: 1;
  width: 15px;
  height: 15px;
  border: 1px solid #777;
  background: rgba(60,60,60,255);
  margin-left: 5px;
  -webkit-animation: isChecking 0.25s linear 1;
          animation: isChecking 0.25s linear 1;
}

@-webkit-keyframes isChecking {
  0% { background: rgba(255,255,255,255); }
  100% { background: rgba(80,80,80,255) }
}

@keyframes isChecking {
  0% { background: rgba(255,255,255,255); }
  100% { background: rgba(80,80,80,255) }
}




