
.ProjectSelectorText {
    color: #FFF;
    margin: 5px;
    font-family: 'Segoe UI', Lucinda Grande, Geneva, Verdana, sans-serif;
    font-size: 16pt;
    -webkit-user-select: none;
    -moz-user-select: none;
     -ms-user-select: none;
         user-select: none;
    line-height: 50px;
}

.ProjectSelectorContainer {
  height: 50px;
}

.ProjectSelectorFlexContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
}

.ProjectSelectorLabelContainer {
  -ms-flex: 3 1;
      flex: 3 1;
}

.ProjectSelectorIconContainer {
  -ms-flex-positive: 0;
      flex-grow: 0;
  height: 16px;
}

.ProjectSelectorIcon {
  float: right;
  margin: 0px 2px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
}

.ProjectSelectorIcon[data-colour*="Red"] {
  background: #F00;
}

.ProjectSelectorIcon[data-colour*="Blue"] {
  background: #01D;
}

.ProjectSelectorIcon[data-colour*="Yellow"] {
  background: #FF9300;
}

.ProjectSelectorIcon[data-colour*="Green"] {
  background: #22B30B;
}

.ProjectSelectorIcon[data-count*="0"] {
  display: none;
}

.ProjectSelectorIconText {
  display: block;
  font-family: 'Segoe UI', Lucinda Grande, Geneva, Verdana, sans-serif;
  font-size: 12pt;
  color: #FFF;
  text-align: center;
  line-height: 24px;
  margin: 0px auto;
}

#projectSelectorInput {
  color: #FFF;
  margin: auto 5px;
  font-family: 'Segoe UI', Lucinda Grande, Geneva, Verdana, sans-serif;
  font-size: 16pt;
  background: rgba(100,100,100,255);
  border: 0px solid #FFF;
}


.ProjectSelectorActiveStyle {
  background: rgba(63,63,76,255);
  height: 100%;
}


