.DueDateContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: end;
      justify-content: flex-end;
}

.DueDate {
  position: relative;
  border-Radius: 50%;
  width: 32px;
  height: 32px; /* Set line-height of .DueDateText equal to this */
  color: #FFF;
  -ms-flex-positive: 1;
      flex-grow: 1;
  margin-right: 5px;
}

.DueDateText {
  display: block;
  font-family: 'Segoe UI', Lucinda Grande, Geneva, Verdana, sans-serif;
  font-size: 7pt;
  font-weight: 700;
  text-align: center;
  margin: 0 auto;
  line-height: 36px; /* Set height: of .DueDate Equal to This */
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



.Complete {
  background: #444;
  height: 32px;
  width: 32px;
}

.Overdue {
  background: #FF0000;
}

.Soon {
  background: #FF9300;
}

.Today {
  background: #00D;
}

.Today .DueDateText {
  font-weight: 700;
  font-stretch: extra-condensed;
}

.Later {
  background: #22B30B;
}

.NotSet {
  background: transparent;
  border: 1px solid #777;
  height: 32px;
  width: 32px;
}


